<template>
  <div class="teacher-edit">
    <el-form :model="form" :rules="rules" label-position="left" ref="form" label-width="80px" class="form">
      <div class="separated-text">基本信息</div>
      <el-form-item class="item" label="姓名" prop="name">
        <el-input class="input" v-model="form.name" placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item class="item" label="性别">
        <el-radio-group v-model="form.sex">
          <el-radio :key="key" v-for="(item,key) in $enums.Sex.list" :label="item.value" border>{{item.label}}&nbsp;&nbsp;&nbsp;&nbsp;</el-radio>
        </el-radio-group>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="出生年月">
        <el-date-picker
            :picker-options="pickerOptions"
            v-model="form.birthday"
            type="date" class="input"
            placeholder="选择日期">
        </el-date-picker>
        <span class="append">{{age}}岁</span>
      </el-form-item>
      <el-form-item class="item" label="当前状态" prop="jobStatus">
        <el-radio-group v-model="form.jobStatus">
          <el-radio :key="key" v-for="(item,key) in $enums.JobStatus.list" :label="item.value" border>{{item.label}}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="身份证号">
        <el-input :maxLength="18" v-model="form.idCard" placeholder="请输入身份证号"></el-input>
      </el-form-item>
      <el-form-item class="item" label="联系电话" prop="phone">
        <el-input :maxLength="11" v-model="form.phone" placeholder="请输入联系电话"></el-input>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="民族">
        <el-select filterable default-first-option class="input" v-model="form.folk">
          <el-option
              v-for="item in nationList"
              :label="item.nation" :value="item.id" :key="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="item" label="籍贯">
        <el-input v-model="form.residence" placeholder="请输入籍贯"></el-input>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="政治面貌">
        <el-select class="input" clearable v-model="form.politicsStatus">
          <el-option v-for="item in $enums.PoliticsStatus.list"
                     :value="item.value" :label="item.label" :key="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="item" label="加入时间">
        <el-date-picker
            :picker-options="pickerOptions"
            v-model="form.joinPartyTime"
            type="date" class="input"
            placeholder="选择加入时间">
        </el-date-picker>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="第一学历">
        <el-select class="input" clearable v-model="form.firstEducation">
          <el-option v-for="item in $enums.Education.list"
                     :value="item.value" :label="item.label" :key="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="item" label="最高学历">
        <el-select class="input" clearable v-model="form.highestEducation">
          <el-option v-for="item in $enums.HighestEducation.list"
                     :value="item.value" :label="item.label" :key="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="毕业时间">
        <el-date-picker
            :picker-options="pickerOptions"
            v-model="form.graduateTime"
            type="date" class="input"
            placeholder="选择毕业时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item class="item" label="毕业院校">
        <el-input v-model="form.graduateSchool" placeholder="请输入毕业院校"></el-input>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item long-label" label="参加工作时间">
        <el-date-picker
            :picker-options="pickerOptions"
            v-model="form.joinWorkTime"
            type="date" class="input"
            placeholder="参加工作时间">
        </el-date-picker>
        <span class="append">{{workYears}}年</span>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="来校时间">
        <el-date-picker
            v-model="form.joinSchoolTime"
            :picker-options="pickerOptions"
            type="date" class="input"
            placeholder="请选择来校时间">
        </el-date-picker>
        <span class="append">{{schoolYears}}年</span>
      </el-form-item>
      <el-form-item class="item long-label" label="原工作单位">
        <el-input v-model="form.lastWorkUnit" placeholder="请输入原工作单位"></el-input>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="职称">
        <table class="table">
          <thead>
          <tr>
            <th width="40%">职称名称</th>
            <th width="40%">评定时间</th>
            <th width="20%">操作</th>
          </tr></thead>
          <tbody>
          <tr v-for="(item,key) in form.teacherTitleList">
            <td>{{$enums.TeacherTitleType.getName(item.title)}}</td>
            <td>{{item.acquireTime | substrTime}}</td>
            <td>
              <span @click="form.teacherTitleList.splice(key,1)" class="oper">删除</span>
            </td>
          </tr>
          <tr v-show="showTitleForm">
            <td>
              <el-select default-first-option class="input" size="small" filterable clearable v-model="titleForm.title">
                <el-option v-for="item in $enums.TeacherTitleType.list"
                           :disabled="disabledOption(item)"
                           :value="item.value" :label="item.label" :key="item.value"
                ></el-option>
              </el-select>
            </td>
            <td>
              <el-date-picker
                  v-model="titleForm.time"
                  size="small"
                  :picker-options="pickerOptions"
                  type="date" class="input"
                  placeholder="请选择评定时间">
              </el-date-picker>
            </td>
            <td>
              <span @click="addTitle" class="oper">完成</span>
              <span @click="showTitleForm = false;titleForm.title = titleForm.time = null" class="oper">删除</span>
            </td>
          </tr>
          <i @click="showTitleFormFun()" class="icon el-icon-circle-plus"></i>
          </tbody>
        </table>
      </el-form-item>
      <div class="clear"></div>
      <!--在校信息-->
      <div class="separated-text">在校信息</div>
      <el-form-item class="item" label="所属部门" prop="orgIds">
        <el-input disabled class="input" v-model="selectedDepName.join()" placeholder="请选择所属部门">
          <el-button  @click="showDepDialog" class="hand" slot="append">选择部门</el-button>
          <!--<span @click="" class="hand" slot="append"></span>-->
        </el-input>
      </el-form-item>
      <el-form-item class="item" label="编制" prop="formationType">
        <el-select default-first-option class="input" clearable filterable v-model="form.formationType">
          <el-option :label="item.label" :value="item.value" :key="item.value"
                     v-for="item in $enums.FormationType.list"></el-option>
        </el-select>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="年级">
        <el-select class="input" v-model="form.gradeId">
          <el-option
              v-for="item in gradeList"
              :label="item.name" :value="item.id" :key="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="item" label="教授科目">
        <el-select default-first-option class="input" filterable multiple v-model="form.courseIdList">
          <el-option
              v-for="item in subjectList"
              :label="item.name" :value="item.id" :key="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="岗位" prop="teacherPositionList">
        <table class="table">
          <thead>
          <tr>
            <th width="40%">岗位名称</th>
            <th width="20%">操作</th>
          </tr></thead>
          <tbody>
          <tr v-for="(item,key) in form.teacherPositionList">
            <td>{{getPositionName(key)}}</td>
            <td>
              <span @click="form.teacherPositionList.splice(key,1)" class="oper">删除</span>
            </td>
          </tr>
          <tr v-show="positionForm">
            <td>
              <!--<el-cascader-->
                  <!--size="small" class="input"-->
                  <!--v-model="position"-->
                  <!--:options="positionList"-->
                  <!--placeholder="请选择岗位"-->
              <!--&gt;</el-cascader>-->
            </td>
            <td>
              <span @click="addPosition" class="oper">完成</span>
              <span @click="positionForm = false;position = []" class="oper">删除</span>
            </td>
          </tr>
          <i @click="positionForm = true" class="icon el-icon-circle-plus"></i></tbody>
        </table>
      </el-form-item>

      <div class="clear"></div>
      <!--家庭信息-->
      <div class="separated-text">家庭信息</div>
      <el-form-item class="item" label="家庭电话">
        <el-input :maxLength="11" v-model="form.homeTel" type="tel" placeholder="请输入家庭电话"></el-input>
      </el-form-item>
      <div class="clear"></div>
      <el-form-item class="item" label="子女信息">
        <table class="table long-table">
          <thead>
          <tr>
            <th width="15%">子女姓名</th>
            <th width="13%">性别</th>
            <th width="12%">年龄</th>
            <th width="22%">学校或单位</th>
            <th width="25%">备注</th>
            <th width="13%">操作</th>
          </tr></thead>
          <tbody>
          <tr v-for="(item,key) in form.teacherChildList">
            <td>{{item.name}}</td>
            <td>{{$enums.Sex.getName(item.sex)}}</td>
            <td>{{item.age}}</td>
            <td>{{item.unit}}</td>
            <td>{{item.remark}}</td>
            <td>
              <span @click="form.teacherChildList.splice(key,1)" class="oper">删除</span>
            </td>
          </tr>
          <tr v-show="showChildForm">
            <td>
              <el-input v-model="childForm.name" size="small" placeholder="子女姓名"></el-input>
            </td>
            <td>
              <el-select v-model="childForm.sex" size="small">
                <el-option v-for="item in $enums.Sex.list"
                           :value="item.value" :label="item.label" :key="item.value"
                ></el-option>
              </el-select>
            </td>
            <td>
              <el-input type="tel" v-model="childForm.age" size="small" placeholder="年龄"></el-input>
            </td>
            <td>
              <el-input v-model="childForm.unit" size="small" placeholder="学校或单位"></el-input>
            </td>
            <td>
              <el-input v-model="childForm.remark" size="small" placeholder="备注"></el-input>
            </td>
            <td>
              <span @click="addChild" class="oper">完成</span>
              <span @click="showChildForm = false" class="oper">删除</span>
            </td>
          </tr>
          <i @click="showChildFormFun()" class="icon el-icon-circle-plus"></i></tbody>
        </table>
      </el-form-item>
    </el-form>
    <!--选择部门弹窗-->
    <el-dialog :visible.sync="depDialog" class="dep-dialog" width="400px">
      <div class="dep-form">
        <p class="title">{{depTitle}}</p>
        <div class="content">
          <item :data="depData" :selectedDep.sync="selectedDep"
                :selectedDepName.sync="selectedDepName"
          ></item>
        </div>
      </div>
      <div class="btns">
        <div @click="depDialog = false" class="btn-submit">关闭</div>
      </div>
    </el-dialog>
    <div class="btns mt15 mb30">
      <div @click="doSubmit()" class="btn-submit btn-add mr15">{{$route.query.id ? '保存': '提交'}}</div>
      <div @click="$router.go(-1)" class="btn-clear">返回</div>
    </div>
  </div>
</template>
<script>
  // 复用组织机构中的组件
  import item from '../../school/user/Item'

  export default {
    components: {
      item
    },
    data() {
      var checkDep = (rule, value, callback) => {
        if (this.selectedDepName.length === 0) {
          callback(new Error('请选择所属部门'))
        } else {
          callback();
        }
      }
      var checkPosition = (rule, value, callback) => {
        if (this.position && this.position.length) {
          callback();
        } else if (this.form.teacherPositionList.length > 0) {
          callback()
        } else {
          callback(new Error('请选择岗位'))
        }
      }
      var checkTeacherTitleList = (rule, value, callback) => {
        if (this.titleForm.title) {
          callback()
        }else if(this.form.teacherTitleList.length > 0){
          callback()
        } else {
          callback(new Error('请选择职称'))
        }
      }
      return {
        // 是否为编辑页
        editPage: this.$route.path.includes('addEdit'),
        // 是否为详情页
        detailPage: this.$route.path.includes('detail'),
        // 科目列表
        subjectList: [],
        // 年级列表
        gradeList: [],
        form: {
          name: '',
          sex: 'Male',
          jobStatus: 'SERVING',
          orgIds: [1],
          courseIdList: [],
          gradeId: '',
          idCard: '',
          phone: '',
          birthday: null,
          age: null,
          folk: '',
          residence: '',
          teacherPositionList: [],
          joinPartyTime: null,
          politicsStatus: null,
          firstEducation: null,
          highestEducation: null,
          graduateTime: null,
          graduateSchool: '',
          joinWorkTime: null,
          workYears: '',
          joinSchoolTime: null,
          lastWorkUnit: '',
          teacherTitleList: [],
          homeTel: '',
          formationType: null,
          teacherChildList: []
        },
        rules: {
          teacherPositionList: {required: true, validator: checkPosition, trigger: 'change'},
          name: {required: true, message: '请输入姓名', trigger: 'blur'},
          // sex: {required: true, message: '请选择性别', trigger: 'change'},
          formationType: {required: true, message: '请选择编制', trigger: 'change'},
          jobStatus: {required: true, message: '请选择状态', trigger: 'change'},
          orgIds: {required: true, validator: checkDep, trigger: 'change'},
          // idCard: [{required: true, message: '请输入身份证号码', trigger: 'blur'},
          phone: [{required: true, message: '请输入联系电话', trigger: 'blur'},
            {min: 11, max: 11, message: '长度为11位数字', trigger: 'blur'},
          ],
          // teacherTitleList: {required: true, validator: checkTeacherTitleList, trigger: 'change'},
          // highestEducation: {required: true, message: '请选择最高学历', trigger: 'change'},
        },
        // 添加职称是否展现
        showTitleForm: false,
        // 添加职称表单
        titleForm: {
          title: '',
          time: null
        },
        // 添加子女是否展现
        showChildForm: false,
        // 添加子女表单
        childForm: {
          name: '',
          sex: 'Male',
          unit: '',
          remark: '',
          age: '',
        },
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
        },
        // 选择所属部门弹窗
        depDialog: false,
        // 一级部门名称
        depTitle: '',
        // 部门数据
        depData: [],
        // 选择的部门ID
        selectedDep: [],
        // 选择的部门名称
        selectedDepName: [],
        // 民族列表
        nationList: [],
        // 展示添加岗位表单
        positionForm: false,
        // 岗位
        position: [],
        // 所有的岗位
        positionList: []

      }
    },
    methods: {
      // 获取所有民族
      async getNationList() {
        this.nationList = await this.$ajax({
          url: '/common/queryNationList.do'
        })
      },
      // 根据岗位ID获取岗位名称
      getPositionName(key) {
        let text = ''
        let arr = this.form.teacherPositionList[key]
        this.positionList.forEach(e=>{
          if(e.value === arr.firstPositionId){
            text += e.label
            if(e.children){
              e.children.forEach(n=>{
                if(n.value === arr.secondPositionId){
                  text += '/' + n.label
                }
              })
            }
          }
        })
        return text
      },
      // 获取所有岗位
      async getPosition() {
        this.positionList = await this.$ajax({
          url: '/dict/queryTeacherPositionCascade.do'
        })
      },
      // 获取所有的部门
      async getAllDep() {
        let res = await this.$ajax({
          url: '/org/queryOrganizationTree.do',
          query: {
            orgTypes: ['ADMIN_ORG', 'TEACH_ORG']
          }
        })
        this.depTitle = res.orgName
        this.depData = recursion(res.subOrgList, 1)

        // 递归给每一个item设置缩进和check-box隐藏
        function recursion(arr, i) {
          arr.forEach(e => {
            e.tier = i
            e.status = false
            if (e.subOrgList.length > 0) {
              recursion(e.subOrgList, i + 1)
            }
          })
          return arr
        }

      },
      // 禁用已经选择的职称
      disabledOption(item) {
        let arr = this.form.teacherTitleList
        for (let i = 0; i < arr.length; i++) {
          if (item.value === arr[i].title) {
            return true
          }
        }
        return false
      },
      // 获取年级列表
      async getGradeList() {
        this.gradeList = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
      },
      // 获取教授科目
      async getSubject() {
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.subjectList = res
      },
      // 展现选择部门弹窗
      showDepDialog() {
        this.depDialog = true
        if (!this.depTitle) {
          this.getAllDep()
        }
      },
      // 展现添加职称表单
      showTitleFormFun() {
        this.showTitleForm = true
      },

      // 添加职称
      addTitle() {
        if (!this.titleForm.title) {
          this.$message.warning('请输入职称名称')
          return
        }
        if (!this.titleForm.time && this.titleForm.title !== 'RANK_UNDECIDED') {
          this.$message.warning('请输入评定时间')
          return
        }
        this.form.teacherTitleList.push({
          title: this.titleForm.title,
          acquireTime: this.titleForm.time ? this.titleForm.time.$Format('yyyy-MM-dd') + ' 00:00:00' : null
        })
        this.showTitleForm = false
        this.$refs.form.clearValidate(['teacherTitleList'])
        this.titleForm = {
          title: '',
          time: null
        }
      },
      // 添加岗位
      addPosition() {
        if (!this.position) {
          this.$message.warning('请选择岗位')
          return
        }
        this.form.teacherPositionList.push({
          firstPositionId: this.position[0],
          secondPositionId: this.position[1] || null,
        })
        this.positionForm = false
        this.position = []
      },
      // 展现添加子女信息表单
      showChildFormFun() {
        this.showChildForm = true
      },
      // 添加子女
      addChild() {
        if (!this.childForm.name) {
          this.$message.warning('请输入姓名')
          return
        }
        // 判断年龄
        if (this.childForm.age && isNaN(parseInt(this.childForm.age))) {
          this.$message.warning('年龄请输入正整数')
          return
        }
        if (parseInt(this.childForm.age) < 0) {
          this.$message.warning('年龄请输入正整数')
          return
        }
        this.form.teacherChildList.push(this.childForm)
        this.showChildForm = false
        this.childForm = {
          name: '',
          sex: 'Male',
          unit: '',
          remark: '',
          age: '',
        }

      },
      // 获取教职工详情
      async getDetail() {
        let res = await this.$ajax({
          url: '/teacher/getTeacherDetail.do',
          query: {
            id: this.$route.query.id
          }
        })
        res.courseIdList = res.courseList.map(e=> e.id)
        this.selectedDep = []
        this.selectedDepName = []
        res.orgList.forEach(e=>{
          this.selectedDep.push(e.id)
          this.selectedDepName.push(e.name)
        })
        this.form = res
      },
      // 提交
      async doSubmit() {
        var doIt = async () => {
          let query = JSON.parse(JSON.stringify(this.form))
          // 判断长度
          if(query.residence && query.residence.length > 20){
            this.$message.warning('籍贯不能超过20个字符')
            return
          }
          if(query.graduateSchool && query.graduateSchool.length > 16){
            this.$message.warning('毕业院校不能超过16个字符')
            return
          }
          if(query.lastWorkUnit && query.lastWorkUnit.length > 20){
            this.$message.warning('原工作单位不能超过20个字符')
            return
          }
          // 处理所属部门
          query.orgIds = this.selectedDep
          // 处理各种时间
          query.birthday && (query.birthday = new Date(query.birthday).$Format('yyyy-MM-dd') + ' 00:00:00')
          query.joinPartyTime && (query.joinPartyTime = new Date(query.joinPartyTime).$Format('yyyy-MM-dd') + ' 00:00:00')
          query.graduateTime && (query.graduateTime = new Date(query.graduateTime).$Format('yyyy-MM-dd') + ' 00:00:00')
          query.joinWorkTime && (query.joinWorkTime = new Date(query.joinWorkTime).$Format('yyyy-MM-dd') + ' 00:00:00')
          query.joinSchoolTime && (query.joinSchoolTime = new Date(query.joinSchoolTime).$Format('yyyy-MM-dd') + ' 00:00:00')
          let url = '/teacher/addTeacher.do'
          let text = '新增成功'
          if(this.$route.query.id){
            url = '/teacher/updateTeacher.do'
            text = '修改成功'
          }
          await this.$ajax({
            url,
            el: '.btn-add',
            noText: true,
            query,
          })
          this.$remind(text)
          this.$router.push('/personnel/teacher')
        }
        await this.$refs.form.validate((valid) => {
          if (!valid) {
            this.$message.warning('请将必填项填写完整')
            return false
          } else {
            doIt()
          }
        })

      },
      // 计算年龄 工龄 校龄时间
      computedYear(val) {
        if (!val) {
          return '--'
        }
        let startDate = new Date(val).$Format('yyyy-MM-dd')
        let endDate = new Date().$Format('yyyy-MM-dd')
        let sy = parseInt(startDate.split('-')[0])
        let ey = parseInt(endDate.split('-')[0])
        let sm = parseInt(startDate.split('-')[1])
        let em = parseInt(endDate.split('-')[1])
        let sd = parseInt(startDate.split('-')[2])
        let ed = parseInt(endDate.split('-')[2])
        if (ed > sd) {
          em++
        } else {
          em--
        }
        if (em > sm) {
          ey++
        }
        let age = ey - sy - 1
        return age >= 0 ? age : 0
      }
    },
    computed: {
      // 出生年月切换自动计算年龄
      'age': function () {
        return this.computedYear(this.form.birthday)
      },
      'workYears': function () {
        return this.computedYear(this.form.joinWorkTime)
      },
      'schoolYears': function () {
        return this.computedYear(this.form.joinSchoolTime)
      }
    },
    mounted() {
      if (this.$route.query.id) {
        this.getDetail()
      }
      this.getPosition()
      this.getSubject()
      this.getGradeList()
      this.getNationList()
    }
  }
</script>
